@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;1,100&display=swap);
* {
  box-sizing: border-box;
}

:root {
  --BGC-header: #032541;
  --posi-header-bar: 0;
}

body {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: white;
}

#root .app {
  width: 100%;
  height: 200vh;
  position: relative;
}
#root .app .header {
  background-color: #032541;
  background-color: var(--BGC-header);
  position: fixed;
  width: 100%;
}
#root .app .header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#root .app .header-bar div {
  display: flex;
}
#root .app .header-bar-link {
  font-size: 25px;
  color: white;
  margin: 10px 10px 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#root .app .header-bar-link .home {
  width: 80px;
  object-fit: cover;
}
#root .app .header-bar-link-icon-infront {
  font-size: 30px;
}
#root .app .header-bar-link-icon-back {
  font-size: 15px;
}
#root .app .header-bar-text {
  padding: 5px;
}
#root .app .header-bar-search {
  margin-right: 4%;
  width: 50px;
  height: 50px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-weight: 500;
  border: none;
  background-color: transparent;
  color: #19d0b2;
  border: none;
  outline: none;
}
#root .app .header-search {
  background-color: #fff;
}
#root .app .header-search-tool {
  border-bottom: 1px solid grey;
  display: flex;
  justify-content: left;
  align-items: center;
}
#root .app .header-search-tool .search-icon {
  margin: 5px;
  margin-left: 4%;
  font-size: 25px;
}
#root .app .header-search-tool-filled {
  margin: 5px;
  font-size: 25px;
  border: none;
  outline: none;
  padding: 15px;
  width: 90%;
  margin-left: 20px;
}
#root .app .header-search-tool .close-icon {
  margin: 5px;
  margin-right: 4%;
}
#root .app .header-search-tool .close-icon-wait {
  width: 20px;
  height: 20px;
  position: relative;
  display: inline-block;
}
#root .app .header-search-tool .close-icon-wait::after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  border: 1px solid grey;
  border-radius: 50%;
  z-index: 2;
  clip: rect(0, 18px, 20px, 2px);
  box-sizing: border-box;
  -webkit-animation: header-search-icon-wait-outside 0.5s linear 0s 1;
          animation: header-search-icon-wait-outside 0.5s linear 0s 1;
}
@-webkit-keyframes header-search-icon-wait-outside {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes header-search-icon-wait-outside {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
#root .app .header-search-tool .close-icon-wait::before {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  border: 1px solid grey;
  border-radius: 50%;
  clip: rect(2px, 10px, 9px, 0px);
  box-sizing: border-box;
  top: 5px;
  left: 5px;
  -webkit-animation: header-search-icon-wait-inner 0.5s linear 0s var(--run-search-icon-wait);
          animation: header-search-icon-wait-inner 0.5s linear 0s var(--run-search-icon-wait);
}
@-webkit-keyframes header-search-icon-wait-inner {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
@keyframes header-search-icon-wait-inner {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
#root .app .header-search-tool .close-icon-display {
  font-size: 20px;
}
#root .app .header-search-result-trending {
  border-bottom: 1px solid grey;
  padding-top: 5px;
  padding-bottom: 5px;
}
#root .app .header-search-result-trending .trending-icon {
  margin: 5px 0 5px 4%;
  font-size: 30px;
  font-weight: bolder;
}
#root .app .header-search-result-trending .trending-text {
  font-size: 40px;
  font-weight: bold;
  padding-left: 10px;
}
#root .app .header-search-result-items {
  border-bottom: 1px solid grey;
  padding-top: 5px;
  padding-bottom: 5px;
}
#root .app .header-search-result-items .icon {
  margin: 5px 0 5px 4%;
  font-size: 20px;
  font-weight: bolder;
}
#root .app .header-search-result-items .text {
  font-size: 25px;
  margin-left: 20px;
}

.link-decoration {
  text-decoration: none;
}

